<template>
  <side-window>
    <div class="education social_media" ref="containerScroll">
      <div class="my_education sideScroll" ref="myEducation">
        <h1>
          EDUCATION
        </h1>
        <div class="my_certificate">
          <img @click="look_at_cert" src="../assets/images/certificate.svg" class="certificate" alt="certificate">
          <img-show
              v-model:show="show">
            <img class="imgShow" ref="certImg" src="../assets/images/certificate.svg" alt="certificate">
          </img-show>
          <div class="certificate_text">
            <h2><a class="skillfactory_link" href="https://skillfactory.ru/python-for-web-developers" target="_blank">SKILLFACTORY.RU</a> CERTIFICATE</h2>
            <div class="certificate_list">
            <p>I took this course for 1.5 years. What is taught on it:</p>
            <ul>
              <li v-for="component in skillfactory_list" :key="component">
                {{component}}
              </li>
            </ul>
            </div>
          </div>
        </div>
        <p class="education_text">
          I also study at a college which is located in my hometown Chernihiv. Specialty - computer engineer.<br>
          But in it we have been learning Paskal, Excel, soldering microcircuits, etc. for all these 3 years. So all my knowledge is self-study.
        </p>
      </div>
      <div class="my_social_media sideScroll" ref="socialMedia">
        <h1>
          SOCIAL NETWORKS
        </h1>
        <div class="social">
          <img src="@/assets/images/github.png" alt="" @click="githubPage">
          <img src="@/assets/images/linkedin.png" alt="" @click="linkedinPage">
        </div>
        <p class="education_text social_text">
          On these sites you can find out more information about me (for example, that I would be happy to work in a team and communicate well with different people:)).<br>
          And see some of the tasks that I gradually did during the course
        </p>
      </div>
      <div class="arrow_button">
        <button class="up_arrow" ref="upBtn"
                @click="upScrollBtn"
                @mouseenter="scaleUpArr" @mouseleave="unScaleUpArr">
          <span class="up_arr btn_icon" ref="upArr"></span>
        </button>
        <button class="down_arrow" ref="downBtn"
                @click="scrollDownBtn"
                @mouseenter="scaleDownArr" @mouseleave="unScaleDownArr">
         <span class="down_arr btn_icon" ref="downArr"></span>
        </button>
      </div>
    </div>
  </side-window>
</template>

<script>
import SideWindow from "@/components/UI/SideWindow";
import ImgShow from "@/components/UI/ImgShow";
export default {
  name: "SideMySocialMedia",
  components: {ImgShow, SideWindow},
  data() {
    return {
      skillfactory_list: ['• HTML and CSS', '• JavaScript and Bootstrap', '• Python', '• SQL', '• Django', '• Linux',
        '• Work with freelance orders'],
      show: false,
      activePanel: null,
      touchStartSide: 0,
    }
  },
  mounted() {
    this.scrollListenerSide();
    this.$refs.upBtn.style.background = 'rgba(33, 33, 33, 0.2)'
    this.$refs.upBtn.disabled;
  },
  methods: {
    githubPage() {
      window.open('https://github.com/Illmix', '_blank').focus();
    },
    linkedinPage() {
      window.open('https://www.linkedin.com/in/ilya-chaban-68971b238/', '_blank').focus();
    },
    scrollDownBtn(e) {
      this.$smoothScroll({
        container: this.$refs.containerScroll,
        scrollTo: this.$refs.socialMedia,
      })
      this.$refs.downBtn.style.background = 'rgba(33, 33, 33, 0.2)'
      this.$refs.upBtn.style.background = 'rgba(33, 33, 33, 0.8)'
      this.$refs.downBtn.disabled = true;
      this.$refs.upBtn.disabled = false;
    },
    upScrollBtn(e) {
      this.$smoothScroll({
        container: this.$refs.containerScroll,
        scrollTo: this.$refs.myEducation,
      })
      this.$refs.upBtn.style.background = 'rgba(33, 33, 33, 0.2)'
      this.$refs.downBtn.style.background = 'rgba(33, 33, 33, 0.8)'
      this.$refs.upBtn.disabled = true;
      this.$refs.downBtn.disabled = false;
    },
    scaleUpArr() {
      this.$refs.upArr.style.transform = 'scale(0.9)';
    },
    unScaleUpArr() {
      this.$refs.upArr.style.transform = 'scale(0.8)';
    },
    scaleDownArr() {
      this.$refs.downArr.style.transform = 'scale(0.9)';
    },
    unScaleDownArr() {
      this.$refs.downArr.style.transform = 'scale(0.8)';
    },
    look_at_cert() {
      this.show = true;
    },
    scrollListenerSide() {
      this.$refs.socialMedia.addEventListener('wheel', this.socialScroll)
      this.$refs.myEducation.addEventListener('wheel', this.educationScroll)
      this.$refs.socialMedia.addEventListener('keydown', this.socialScroll)
      this.$refs.myEducation.addEventListener('keydown', this.educationScroll)
    },
    deleteListenerSide() {
      this.$refs.socialMedia.removeEventListener('wheel', this.socialScroll)
      this.$refs.myEducation.removeEventListener('wheel', this.educationScroll)
      this.$refs.socialMedia.removeEventListener('keydown', this.socialScroll)
      this.$refs.myEducation.removeEventListener('keydown', this.educationScroll)
    },
    educationScroll(e) {
      if (e.deltaY > 0) {
        this.deleteListenerSide();
        this.$smoothScroll({
          container: this.$refs.containerScroll,
          scrollTo: this.$refs.socialMedia,
        })
        this.$refs.downBtn.style.background = 'rgba(33, 33, 33, 0.2)'
        this.$refs.upBtn.style.background = 'rgba(33, 33, 33, 0.8)'
        this.$refs.downBtn.disabled = true;
        this.$refs.upBtn.disabled = false;
        setTimeout(()=>{this.scrollListenerSide()}, 600)
      }
    },
    socialScroll (e) {
      if (e.deltaY < 0) {
        this.deleteListenerSide();
        this.$smoothScroll({
          container: this.$refs.containerScroll,
          scrollTo: this.$refs.myEducation,
        })
        this.$refs.upBtn.style.background = 'rgba(33, 33, 33, 0.2)'
        this.$refs.downBtn.style.background = 'rgba(33, 33, 33, 0.8)'
        this.$refs.upBtn.disabled = true;
        this.$refs.downBtn.disabled = false;
        setTimeout(()=>{this.scrollListenerSide()}, 600)
      }
    },

  }
}
</script>

<style scoped>
  .social_media {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
    width: 100%;
    height: 100%;
  }
  .imgShow {
    width: 100%;
    height: 100%;
  }
  .skillfactory_link {
    text-decoration: none;
    color: black;
  }

  .my_education, .my_social_media {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    min-height: 100vh;
    width: 100%;
  }

  .my_education {
    background-image: url("@/assets/images/backgrounds/green.png");
  }

  .my_social_media {
    background-image: url("@/assets/images/backgrounds/greywhite.jpg");
  }

  .my_education h1, .my_social_media h1 {
    margin-top: 3%;
    font-size: 60px;
  }

  .my_certificate {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 90%;
    height: 70%;
  }

  .certificate_text {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 40%;
  }

  .certificate_text h2 {
    display: flex;
    font-size: 40px;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    color: orangered;
  }

  .certificate_text h2 * {
    color: orangered;
    transition: .5s;
  }

  .certificate_text h2  *:hover {
    text-shadow: 2px 2px #888888;
    color: darkgreen;
  }

  .certificate_text p {
    margin-top: 5px;
    font-size: 23px;
    margin-bottom: 5px;
  }

  .certificate_text ul {
    font-size: 21px;
    list-style-type: none;
  }

  .certificate_text ul li {
    text-align: center;
  }

  .certificate {
    width: 30%;
    height: 60%;
    cursor: pointer;
    transition: .7s;
  }

  .education_text {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 70%;
    height: 30%;
    font-size: 25px;
  }
  .social {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 70%;
    height: 30%;
  }
  .social img {
    width: 15%;
    height: 70%;
    cursor: pointer;
    min-width: 200px;
    min-height: 200px;
    transition: .5s;
  }
  .social img:hover {
    transform: scale(1.1);
  }

  .arrow_button {
    position: fixed;
    transition: .7s;
    display: flex;
    flex-direction: column;
    right: -150%;
    bottom: 1.5rem;
    z-index: 3;
    text-align: center;
    margin: 1rem;
  }

  .arrow_button button {
    position: relative;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    display: block;
    background: red;
    transition: .3s;
    border-radius: 50%;
    width: 3.6rem;
    height: 3.6rem;
    outline: none;
    border: 0;
    cursor: pointer;
    background: rgba(33,33,33, 0.8);
    color: #fefefe;
    margin: 1.2rem auto 0;
  }

  .up_arr {
    background-image: url("@/assets/images/up_arrow.png");
    background-position: 50%;
  }

  .down_arr {
    background-image: url("@/assets/images/down_arrow.png");
    background-position: 50%;
  }

  .btn_icon {
    display: block;
    height: 2.1rem;
    line-height: 2.4rem;
    background-size: 70%;
    background-repeat: no-repeat;
    transition: .5s;
    transform: scale(0.8);
  }


  @media only screen and (max-width: 1050px) and (max-height: 600px) and (min-width: 450px){
    .education h1{
      font-size: 24px;
    }
    .my_social_media h1{
      font-size: 24px;
    }
    .certificate {
      width: 100%;
      height: 70%;
    }
    .certificate_text h2 {
      font-size: 20px;
    }
    .certificate_text p, .certificate_text ul {
      font-size: 16px;
    }
    .education_text {
      width: 100%;
      font-size: 12px;
    }
  }
  @media only screen and (max-width: 1050px) and (min-height: 500px){
    .certificate_text h2 {
      font-size: 20px;
    }
  }
  @media only screen and (max-width: 800px) and (min-height: 500px){
    .education {
      justify-content: flex-start;
    }
    .my_social_media{
      justify-content: flex-start;
    }
    .my_social_media h1{
      font-size: 44px;
    }
    .certificate {
      width: 60%;
      height: 30%;
    }
    .certificate_text h2 {
      font-size: 26px;
    }
    .certificate_text {
      flex-direction: row;
      justify-content: space-around;
      width: 100%;
    }
    .education h1{
      font-size: 44px;
    }
    .my_certificate {
      flex-direction: column;
    }
    .certificate_text {
      text-align: center;
    }
    .certificate_text p, .certificate_text ul {
      font-size: 16px;
    }
    .education_text {
      font-size: 16px;
    }
  }
  @media only screen and (max-width: 560px) and (min-height: 500px){
    .education_text {
      font-size: 14px;
    }
    .social_text {
      font-size: 20px;
    }
  }
  @media only screen and (max-width: 450px) and (min-height: 500px){
    .social img {
      min-width: 100px;
      min-height: 100px;
    }
    .certificate {
      width: 100%;
      height: 60%;
    }
    .certificate_text {
      flex-direction: column;
    }
    .my_social_media h1{
      font-size: 40px;
    }
    .education h1{
      font-size: 40px;
    }
    .certificate_text h2 {
      font-size: 24px;
    }
    .certificate_text p, .certificate_text ul {
      font-size: 18px;
    }
    .education_text {
      font-size: 18px;
    }
    .social_text {
      height: 40%;
      font-size: 20px;
    }
  }
  @media only screen and (max-width: 340px) and (min-height: 500px){
    .education_text {
      font-size: 10px;
    }
  }
  @media only screen and (max-width: 450px) {
    .my_education {
      justify-content: space-around;
    }
    .arrow_button button {
      width: 2rem;
      height: 2rem;
    }
    .social img {
      height: 30%;
      min-width: 100px;
      min-height: 100px;
    }
    .my_social_media h1{
      font-size: 30px;
    }
    .my_social_media {
      justify-content: space-around;
    }
    .education_text {
      width: 80%;
    }
    .education_text:nth-child(1) {
      font-size: 50px;
    }
  }
  @media only screen and (max-height: 450px) {
    .arrow_button button {
      width: 2rem;
      height: 2rem;
    }
    .social img {
      min-width: 100px;
      min-height: 100px;
    }
  }
</style>