<template>
  <div class="image" v-if="show" @click="hideDialog" @mousemove="moveImage($event)">
    <div @click.stop class="image__content"
         :style="{transform: 'scale('+imgSize+') skewY('+skewY+'deg) skewX('+skewX+'deg)'}"
         @wheel="changeSize">
      <slot>

      </slot>
    </div>
  </div>
</template>

<script>
import toggleMixin from "@/mixins/toggleMixin";

export default {
  name: 'img-show',
  mixins: [toggleMixin],
  data() {
    return {
      imgSize: 1,
      skewY: 0,
      skewX: 0,
      mouseCenterY: window.innerHeight/2,
      mouseCenterX: window.innerWidth/2,
    }
  },
  methods: {
    changeSize(e) {
      if (1.7 < this.imgSize && -e.deltaY/1000 > 0) {}
      else if (this.imgSize < 0.5 && -e.deltaY/1000 < 0) {}
      else {
        this.imgSize += -e.deltaY/1000
      }
    },
    moveImage(e) {
      if (Math.floor(e.screenY/40)-Math.floor(this.mouseCenterY/40) > 20 || Math.floor(e.screenY/40)-Math.floor(this.mouseCenterY/40) < -20) {}
      else {
        if (Math.floor(e.screenX/40)-Math.floor(this.mouseCenterX/40) > 0) {
          this.skewY = -(Math.floor(e.screenY/40)-Math.floor(this.mouseCenterY/40));
        }
        else {
          this.skewY = Math.floor(e.screenY/40)-Math.floor(this.mouseCenterY/40);
        }
      }
      if (Math.floor(e.screenX/40)-Math.floor(this.mouseCenterX/40) > 10 || Math.floor(e.screenX/40)-Math.floor(this.mouseCenterX/40) < -10) {}
      else {
        if (Math.floor(e.screenY/40)-Math.floor(this.mouseCenterY/40) > 0) {
          this.skewX = -(Math.floor(e.screenX/40)-Math.floor(this.mouseCenterX/40));
        }
        else {
          this.skewX = Math.floor(e.screenX/40)-Math.floor(this.mouseCenterX/40);
        }
      }
    }
  }
}
</script>

<style scoped>
.image {
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image__content {
  display: flex;
  justify-content: center;
  width: 40%;
  height: 50%;
  transition: .3s;
}

</style>