<template>
  <side-window>
    <div class="projects">
      <h1 v-intersection="showProjects">MY PROJECTS</h1>
      <div class="proj_list" v-if="show">
          <project-form
                        v-for="pr in projects"
                        :key="pr.project.id"
                        :pr="pr"/>
      </div>
      <div v-else class="loader_icon">
        <loader/>
      </div>
    </div>
  </side-window>
</template>

<script>
import SideWindow from "@/components/UI/SideWindow";
import ProjectForm from "@/components/ProjectForm";
import Loader from "@/components/UI/Loader";
export default {
  name: "SideMyProjects",
  components: {Loader, ProjectForm, SideWindow},
  data() {
    return {
      projects: [{project: {id: 0, vid: '', post: `${require('@/assets/projects/bot.png')}`,
          title: 'NEW LANGUAGE - SIMPLE.', body: 'Telegram bot that can help you learn new words in the language you want.'}},
        {project: {id: 1, vid: '', post: `${require('@/assets/projects/page_poster.png')}`,
          title: "THIS WEBPAGE:)", body: `Unfortunately, I don't have many projects. That's why this webpage is listed.`}}],
      show: false,
      videoList: [`${require('@/assets/projects/bot.mp4')}`, `${require('@/assets/projects/web.mp4')}`]
    }
  },
  methods: {
    showProjects() {
      for (let j = 0; j<this.projects.length; j++) {
        this.projects[j].project.vid = this.videoList[j];
      }
      this.show = true;
    }
  },
}
</script>

<style scoped>

.projects {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  background-color: yellow;
  height: 100%;
  width: 100%;
  margin-bottom: 5%;
}
.projects h1 {
  font-size: 60px;
  color: #03136b;
}
.proj_list {
  height: 80%;
  display: flex;
  margin-top: 5%;
  align-items: flex-start;
  justify-content: space-around;
}

@media only screen and (max-height: 600px) and (max-width: 900px) {
  .projects h1 {
    font-size: 30px;
    color: #03136b;
  }
  .projects {
    justify-content: flex-start;
  }
}
@media only screen and (min-height: 550px) and (max-width: 450px) {
  .projects h1 {
    font-size: 30px;
    color: #03136b;
  }
  .proj_list {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 10px;
  }
  .projects {
    justify-content: flex-start;
  }
}
</style>